@font-face {
  font-family: "Ycomputer";
  src: url("/public/assets/Font/Ycomputer.ttf") format("truetype");
}
.warring {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 35px 0px 35px;
}
.warring > .warring__title,
.warring .warring__content,
.warring .warring__btn {
  color: #00ff00;
  font-family: "Ycomputer";
  font-style: normal;
  font-weight: 400;
  -webkit-animation-name: slidein;
          animation-name: slidein;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}
.warring > .warring__title {
  font-size: 40px;
  line-height: 46px;
  margin-bottom: 46px;
}
.warring > .warring__content {
  font-size: 23px;
  line-height: 26px;
  text-align: center;
  margin-bottom: 96px;
}
.warring > .warring__btn {
  border: none;
  background-color: black;
  font-weight: 400;
  font-size: 40px;
  line-height: 46px;
  text-shadow: -2px 0 #888888, 0 2px #888888, 2px 0 #888888, 0 -2px #888888;
}

@-webkit-keyframes slidein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slidein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}/*# sourceMappingURL=warring.css.map */